.parent-container {
  background-color: lightskyblue;/* Black */
}

.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; /* Add margin bottom for spacing */
}

.output {
  border: 2px solid #60910b; /* White */
  padding: 20px;
  width: 70%;
  background-color: lightcoral; /* White */
  color: #062382; /* Black */
}

.welcome,
.user-info,
.user-type {
  padding: 10px;
  margin-bottom: 10px;
  color: #03be00; /* Black */
}

.user-type {
  font-weight: bold;
}

.highlight-on-hover {
  box-shadow: 0 4px 8px rgba(6, 102, 119, 0.3); /* Add box-shadow for the 3D effect */
  transition: box-shadow 0.1s ease; /* Add transition effect */
  background-color: #39e1ff; /* White */
  color: #ff0000; /* Black */
  border: none; /* Remove default button border */
  cursor: pointer; /* Add pointer cursor on hover */
  border-radius: 5px;
  padding: 12px 24px; /* Adjust padding */
  font-size: 16px; /* Adjust font size */
}

.highlight-on-hover:hover {
  box-shadow: 0 8px 16px rgba(10, 155, 153, 0.6); /* Increase shadow on hover for more depth */
}

.highlight-on-hover:active {
  background-color: #09148a; /* White */
}

.student-details {
  margin-top: 20px;
  border: 2px solid #3dcd0d; /* White */
  padding: 20px;
  width: 70%;
  display: flex;
  flex-direction: row; /* Display cards side by side */
  flex-wrap: wrap; /* Allow cards to wrap to the next line if necessary */
  justify-content: center;
  background-color: #108fc6; /* White */
  color: #8d26cd; /* Black */
}

.student-card {
  border: 1px solid #000000;
  padding: 10px; /* Reduced padding for a smaller size */
  margin: 10px; /* Add margin between cards */
  background-color: cyan; /* White */
  width: calc(50% - 20px); /* Set width of each card, subtracting margin */
  max-width: 300px; /* Limit max width of the cards */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.5s ease-in-out;
  border-radius: 58px; /* Smooth transition for box-shadow */
}



.student-card h3 {
  margin-top: 5px; /* Add missing unit */
  font-size: 16px; /* Reduced font size of student name */
}

.student-card p {
  margin: 5px 0; /* Reduced paragraph margin */
}

.card-title {
  font-size: 18px;
  margin-bottom: 10px;
}

.cont2 {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}